.header {
  font-family: 'Pacifico', cursive;
  color: rgb(0, 0, 197);
  text-align: center;
  text-shadow: 1px 1px yellow,
    1px -1px yellow,
    -1px -1px yellow,
    -1px 1px yellow;
  background: lightblue;
  box-shadow: inset 0px -1px 5px magenta,
    0px 1px 5px magenta;
  margin: 0 auto 2px auto;
  padding: 15px 0 15px 0;
}
.instructions {
  font-family: 'Pacifico', cursive;
  color: rgb(0, 0, 170);
  text-align: justify;
  background: lightblue;
  border-radius: 5px;
  box-shadow: inset 0px -1px 3px magenta,
  inset -1px 0px 3px magenta,
  inset 0px 1px 3px magenta,
  inset -1px 0px 3px magenta,
    0px 1px 3px magenta,
    0px -1px 3px magenta,
    -1px 0px 3px magenta,
    1px 0px 3px magenta;
  margin: 5px auto 10px auto;
  padding: 15px;  
  width: 80%;
}
.card {
  background: white;
  border: 2px solid yellow;
  border-radius: 75px;
  box-shadow: 1px 1px 5px yellow,
    1px -1px 5px yellow,
    -1px -1px 5px yellow,
    -1px 1px 5px yellow,
    inset 1px 1px 5px blue,
    inset -1px 1px 5px blue,
    inset -1px -1px 5px blue,
    inset 1px -1px 5px blue;
  margin: 2% 1% 2% 4%;
  width: 150px;
  height: 150px;
  cursor: -webkit-grab;
  cursor: grab;
}
.card :active {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}
.imageBox {
  margin: 3%;
  height: 94%;
  width: 94%;
}
.image {
  width: 99%;
  height: 99%;
  border-radius: 100px;
}

@media (max-width: 820px) {
  .card {
    height: 100px;
    width: 100px;
    border-radius: 50px;
  }
}


@media (max-width: 650px) {
  .card {
    height: 50px;
    width: 50px;
    border-radius: 25px;
  }
}
.loser {
  border: 2px solid yellow;
  box-shadow: 1px 1px 5px yellow,
    1px -1px 5px yellow,
    -1px -1px 5px yellow,
    -1px 1px 5px yellow,
    inset 1px 1px 5px blue,
    inset -1px 1px 5px blue,
    inset -1px -1px 5px blue,
    inset 1px -1px 5px blue;
  margin: 2% auto 2% auto;
  width: 400px;
  height: 400px;
  cursor: -webkit-grab;
  cursor: grab;
  background: url(/MemoryGame/static/media/drNefario.2cc16489.jpg) no-repeat;
  background-size: 400px 400px;
}
.winner {  
  border: 2px solid yellow;
  box-shadow: 1px 1px 5px yellow,
    1px -1px 5px yellow,
    -1px -1px 5px yellow,
    -1px 1px 5px yellow,
    inset 1px 1px 5px blue,
    inset -1px 1px 5px blue,
    inset -1px -1px 5px blue,
    inset 1px -1px 5px blue;
  margin: 2% auto 2% auto;
  width: 300px;
  height: 500px;
  cursor: -webkit-grab;
  cursor: grab;
  background: url(/MemoryGame/static/media/gru.29ace23a.jpg) no-repeat;
  background-size: 300px 500px;
}
.display {
  text-align: center;
  color: red;
  font-size: 30px;
  font-weight: bold;
  margin: 300px auto auto auto;
  background: gray;
  box-shadow: inset 1px 1px 5px blue,
  inset -1px 1px 5px blue,
  inset -1px -1px 5px blue,
  inset 1px -1px 5px blue;
}

.endPad {
  padding: 20px 0;
}

@media (max-width: 650px) {
  .loser {
    width: 300px;
    height: 300px;
    background-size: 300px;
  }

  .display {
    margin-top: 200px;
    font-size: 20px;
  }
}
.container {
  width: 90%;
  margin: 0px auto 0px auto;
}
.scoreboard {
  width: 420px;
  margin: 0px auto 0px auto;
  font-family: 'Pacifico', cursive;
}
.score {
  float: left;
  font-size: 130%;
  text-align: center;
  background: lime;
  border-radius: 20px;
  box-shadow: inset 1px 1px 5px blue,
    inset -1px 1px 5px blue,
    inset 1px -1px 5px blue,
    inset -1px -1px 5px blue;
  width: 200px;
  margin: 5px;
  padding: 5px 0 5px 0;
}
.cheer {
  color: rgb(0, 0, 170);
  text-decoration: underline;
  font-size: 130%;
  text-align: center;
  background: lightblue;
  border-radius: 25px;
  box-shadow: 1px 1px 3px magenta,
  1px -1px 3px magenta,
  -1px -1px 3px magenta,
  -1px 1px 3px magenta;
  margin: 7px auto 6px auto;
  width: 80%;
}
.gamebox {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

@media (max-width: 650px) {
  .score {
    width: 45%;
    font-size: 80%;
  }
  .cheer {
    font-size: 100%;
  }
  .scoreboard {
    width: 100%;
  }
}
.footer {
  font-weight: bold;
  text-align: center;
  background: yellow;
  border-radius: 5px;
  box-shadow: 1px 1px 3px blue,
    1px -1px 3px blue,
    -1px -1px 3px blue,
    -1px 1px 3px blue;
  margin: 20px auto 0px auto;
  width: 300px;
  height: 40px;
  bottom: 0;
}
.body {
  background: url(/MemoryGame/static/media/background.97a9f261.jpg) no-repeat;
  background-size: 100% 100%;
  min-height: -webkit-fill-available;
}

